<template>
  <div class="container">
    <!-- 未登录状态 -->
    <view v-if="!userInfo">
      <div class="login">
        <wux-button
          block
          type="positive"
          class="login__button"
          open-type="getUserInfo"
          @getuserinfo="bindGetUserInfo"
        >登录</wux-button>
        <img src="/static/img/back.png">
      </div>
      <wux-dialog id="wux-dialog"/>
    </view>

    <!-- 登录后显示个人中心 -->
    <view v-else>
      <div class="my__top">
        <div class="avatar">
          <img :src="avatarUrl">
        </div>
        <div class="name">{{nickName}}</div>
      </div>
      <wux-cell-group>
        <wux-cell title="企业介绍" @click="Push('/pages/UnitedData/index')"></wux-cell>
      </wux-cell-group>
    </view>
  </div>
</template>

<script>
import store from "../../store";
import { $wuxDialog } from "../../wux/index";
export default {
  computed: {
    userInfo() {
      return store.state.userInfo;
    }
  },
  data() {
    return {
      nickName: "",
      avatarUrl: ""
    };
  },
  methods: {
    alert() {
      $wuxDialog().alert({
        resetOnClose: true,
        title: "提示",
        content: "请先授权!"
      });
    },
    bindGetUserInfo(e) {
      let _this = this;
      if (e.mp.detail.rawData) {
        store.commit("userInfo");
        _this.$router.back();
         wx.showTabBar({});
      } else {
        _this.alert();
      }
      this.GetData();
    },
    GetData() {
      let _this = this;
      wx.getUserInfo({
        success(res) {
          const userInfo = res.userInfo;
          _this.nickName = userInfo.nickName;
          _this.avatarUrl = userInfo.avatarUrl;
        }
      });
    }
  },
  created() {
    // if (this.userInfo) {
    //   wx.showTabBar({});     
    // } else {
    //   wx.hideTabBar({});
    // }
    // this.GetData();
  }
};
</script>

<style scoped>
.login {
  position: fixed;
  width: 100%;
  height: 100%;
  display: block;
  overflow: hidden;
  background: #f8fbff;
}
.login img {
  width: 100%;
  display: block;
  height: 380px;
}
.login__button {
  overflow: hidden;
  position: absolute;
  width: 80%;
  left: 10%;
  bottom: 100px;
}
.my__top {
  background: #3a9cff;
  padding: 20px 30px;
}

.my__top .avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  margin: 10px auto 30px;
  border: 3px solid #ffffff;
}

.my__top .avatar img {
  width: 100%;
  height: 100%;
  display: block;
  float: left;
}

.my__top .name {
  font-weight: bold;
  font-size: 20px;
  color: #ffffff;
  text-align: center;
  margin-bottom: 10px;
}
</style>



